import React from "react";
import { info } from "../../info";
import { Icon } from "@iconify/react";
import { place, typography } from "../../types";
import GithubIcon from "../../assets/GithubIcon.svg";
import LinkedinIcon from "../../assets/LinkedinIcon.svg";
import EmailIcon from "../../assets/EmailIcon.svg";

export const Title = () => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        padding: place.containerPadding,
        justifyContent: "flex-start", // Vertically centers children
        alignItems: "flex-start", // Horizontally centers children
      }}
    >
      <p
        style={{
          fontFamily: typography.fontFamily,
          fontSize: typography.fontSize.h2,
          fontWeight: typography.weight.h2,
          color: typography.color,
          margin: "0px",
        }}
      >
        {info.name}
      </p>
      <p
        style={{
          fontFamily: typography.fontFamily,
          fontSize: typography.fontSize.h3,
          fontWeight: typography.weight.h3,
          color: typography.color,
          margin: "0px",
        }}
      >
        <p
          style={{
            fontFamily: typography.fontFamily,
            fontSize: typography.fontSize.h3,
            fontWeight: typography.weight.h3,
            color: typography.color,
            margin: "0px",
          }}
        >
          {info.roles.map((role, index) => {
            return role + (index === info.roles.length - 1 ? "" : "  ");
          })}
        </p>
      </p>
      <p
        style={{
          fontFamily: typography.fontFamily,
          fontSize: typography.fontSize.h3,
          fontWeight: typography.weight.h3,
          color: typography.color,
          margin: "0px",
        }}
      >
        {info.professions.map((profession, index) => {
          return (
            profession + (index === info.professions.length - 1 ? "" : "  ")
          );
        })}
      </p>
    </div>
  );
};

export const Brief = () => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        gap: "4px",
        justifyContent: "flex-start", // Vertically centers children
        alignItems: "flex-start", // Horizontally centers children
      }}
    >
      <ul
        style={{
          fontFamily: typography.fontFamily,
          fontSize: typography.fontSize.h3,
          fontWeight: typography.weight.h3,
          color: typography.color,
          margin: "0px",
          gap: place.containerPadding,
        }}
      >
        {info.brief.map((brief, index) => (
          <li key={index}>{brief}</li>
        ))}
      </ul>
    </div>
  );
};

export const Contact = () => {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        height: "5vh",
        gap: place.componentPadding,
      }}
    >
      {info.socialLinks.map((socialLink, index) => {
        let icon; // Declare a variable to hold the imported icon component

        // Use a switch statement for cleaner, more scalable logic
        switch (socialLink.label) {
          case "GitHub":
            icon = GithubIcon; // Assign the imported Github icon
            break;
          case "LinkedIn":
            icon = LinkedinIcon; // Assign the imported Linkedin icon
            break;
          case "Email":
            icon = EmailIcon; // Assign the imported Linkedin icon
            break;
          // Add more cases for other icons as needed
          default:
            icon = null; // Handle cases where no icon matches
        }
        //Ensure the icon exists before rendering
        if (!icon) {
          return null; // Don't render anything if no icon is found
        }

        return (
          <div
            key={index}
            style={{
              width: `${100 / info.socialLinks.length}%`,
            }}
          >
            {icon === EmailIcon ? (
              <a href="mailto:jentingc@gmail.com?subject=Contact from your Portfolio.">
                <img
                  src={icon} // Use the imported icon variable here
                  alt={socialLink.label}
                  style={{ height: "5vh", width: "5vh", objectFit: "cover" }}
                />
              </a>
            ) : (
              <a href={socialLink.url}>
                <img
                  src={icon}
                  alt={socialLink.label}
                  style={{ height: "5vh", width: "5vh", objectFit: "cover" }}
                />
              </a>
            )}
          </div>
        );
      })}
      ;
    </div>
  );
};
